import {
  DownOutlined,
  SearchOutlined,
  SettingOutlined,
  SyncOutlined,
} from "@ant-design/icons";
import {
  Table,
  Col,
  Pagination,
  Card,
  Row,
  Form,
  Dropdown,
  Button,
  Input,
  Tooltip,
  Typography,
  Select,
} from "antd";
import { useRouter } from "next/router";
import { useEffect, useState } from "react";

const { Title } = Typography;
const CTable = ({
  onShowSizeChange,
  items,
  handleSearch,
  refresh,
  columns,
  data,
  total,
  scroll,
  loading,
  accessPermission,
  paginate,
  options,
  placeholder,
  value,
  onSelect,
}) => {
  const [menuPath, setMenuPath] = useState("");

  const router = useRouter();
  useEffect(() => {
    let path = router.pathname.slice(1);
    path = path.replaceAll("/", " / ");
    path = path.replaceAll("-", " ");
    let toArray = path.split(" ");

    let toCapitalize = toArray
      .map((word) => {
        return word[0].toUpperCase() + word.substring(1);
      })
      .join(" ");
    setMenuPath(toCapitalize);
  }, []);

  return (
    <Card
      cover={
        <Col
          span={24}
          style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
        />
      }
    >
      <Row>
        <Col span={24}>
          <Title level={3}>{menuPath}</Title>
        </Col>
        {(items || handleSearch || refresh) && (
          <Col span={24}>
            {options && (
              <Row style={{ float: "left" }}>
                <Col span={12}>
                  <Form layout="inline">
                    <Select
                      options={options}
                      style={{ width: 350 }}
                      size="small"
                      placeholder={placeholder}
                      value={value}
                      onChange={onSelect}
                      optionFilterProp="children"
                      filterOption={(input, option) =>
                        (option?.label ?? "")
                          .toLowerCase()
                          .includes(input.toLowerCase())
                      }
                    />
                  </Form>
                </Col>
              </Row>
            )}
            <Row style={{ float: "right" }}>
              <Form layout="inline">
                {accessPermission.create
                  ? items && (
                      <Form.Item>
                        <Dropdown menu={{ items }}>
                          <Button type="primary" size="small">
                            <SettingOutlined />
                            Action
                            <DownOutlined />
                          </Button>
                        </Dropdown>
                      </Form.Item>
                    )
                  : null}
                {handleSearch && (
                  <Form.Item>
                    <Input
                      size="small"
                      placeholder="Search"
                      name="keyword"
                      id="keyword"
                      value={paginate.keyword}
                      onChange={handleSearch}
                      suffix={
                        <Tooltip title="Search">
                          <SearchOutlined />
                        </Tooltip>
                      }
                    />
                  </Form.Item>
                )}
                {refresh && (
                  <Form.Item onClick={refresh}>
                    <Tooltip title="Reload">
                      <Button size="small">
                        <SyncOutlined />
                      </Button>
                    </Tooltip>
                  </Form.Item>
                )}
              </Form>
            </Row>
          </Col>
        )}
        <Col span={24} style={{ paddingTop: "20px" }}>
          <Table
            columns={columns}
            dataSource={data}
            scroll={scroll}
            size="small"
            rowKey="id"
            bordered
            pagination={false}
            loading={loading}
          />
        </Col>
        <Col span={24} style={{ paddingTop: "10px" }}>
          <Pagination
            style={{ float: "right" }}
            showSizeChanger
            onShowSizeChange={onShowSizeChange}
            defaultCurrent={1}
            total={total}
            current={paginate.current_page}
            defaultPageSize={15}
            pageSizeOptions={["15", "30", "50", "100", "200"]}
            onChange={onShowSizeChange}
            size="small"
          />
        </Col>
      </Row>
    </Card>
  );
};

export default CTable;
